body {
  margin-top: -40px;
}

.App {
  text-align: center;
  background-image: url('/public/WechatIMG82.png');
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .btn-text {
    animation: App-logo-spin infinite .8s linear;
  }
  .points1 {
    animation: p1-animate infinite .5s ease;
  }
  .points2 {
    animation: p2-animate infinite .5s ease;
  }
}

.App-header {
  color:#282c34;
  background-color: #282c34;
  background-image: url('/public/wx2.jpeg');
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}
.time {
  position: absolute;
  left: 32px;
  top: 12px;
  font-size: 15px;
  font-weight: bold;
}

.point {
  /* background-color: rgba(0, 0, 0, .8); */
  width: 10px;
  height : 10px;
  border-radius: 50%;
  transform: translate(-5px, -5px);
  font-size: 12px;
  transition: left .2s;
}

.btn-text {
  position: absolute;
  left: 50%;
  top: 455px;
  color: #fff;
  font-size: 22px;
  font-weight: bold;
  transform: translateX(-48%);
}
.btn-time {
  position: absolute;
  left: 50%;
  top: 508px;
  color: #282c34;
  font-size: 17px;
  font-weight: bold;
  transform: scaleY(1.2) translateX(-50%);
}

@keyframes App-logo-spin {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes p1-animate {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes p2-animate {
  0% {
    opacity: 1;
  }
  49% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
